/* Style component GuidePage */

.mainmachines {
    background-color : #f7f7f7;
    font-family : 'Helvetica';
}

.machines {
    width : 300px;
    margin: 30px;
    height : 320px;
    border : 1px solid LightGrey;
    border-radius : 5px;
    background-color : white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction:column;
        flex-direction:column;
}


.titlemachines{
    padding-top: 50px;
    padding-left : 20px;
    font-size : 24px;
    color: rgb(34, 49, 63);
}

.content {
    display : -ms-flexbox;
    display : flex;
    -ms-flex-wrap : wrap;
        flex-wrap : wrap;    
    -ms-flex-pack: center;    
        justify-content: center;
}

.imagestyle {
    width : 100%;
}

h2,p {
    margin-bottom : 5px;
    padding : 5px;
}

h2 {
    text-transform: uppercase;
}

.buttonguide {
    background-color: #22313F;
    padding: 7px;
    border : none;
    color : #FFF;
    margin-left: 5rem;
    margin-top: 12px;
    width: 130px;  
}

.buttonguide:hover{
    background-color: #6BB9F0;
}

/* Style component Exercice */

#blocexercice {
    margin: 50px;
    display: -ms-flexbox;
    display: flex;
}

.blocvideos {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0 0 15px 30px;
    width: 70%;
}

.blocvideos iframe {
    width: 100%;
}

.blocvideos a {
    color:  #22313F;
    margin: 15px 0;
    max-width: 300px;
}

.blocinfos p {
    margin : 20px 0;
    background-color :  #6BB9F0;
    padding: 15px;
    color:  #22313F;
}

.blocinfos {
width: 30%;
}

#info {
background-color: #22313F;
color: #FFF;
width: 100%;
margin-right: 20px;
}

#info h2 {
    padding: 15px 20px;
}

.blocinfos ul {
    padding: 15px;
}
.blocinfos ul li {
margin: 15px;
}
.blocinfos p {
    text-align: left;
}



/* MEDIA QUERIES */

@media screen and (min-width: 320px) and (max-width: 800px) {
#blocexercice {
    display: block;
    margin: 25px;
}
.blocinfos {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
}
.blocvideos {
    margin: 0;
    position: relative;
    width: 100%;
}
.blocvideos iframe {
    width: 100%;
} 
        


}


