main {
    width: 100%;
}
table {
    border-collapse: collapse;
    width: 100%;
    margin: auto;
}

th, td {
    text-align: left;
    padding: 8px;
}

tr:nth-child(even){
    background-color: #f2f2f2
}

.sendbutton {
    background-color : rgb(4, 167, 39);
    color: #FFF;
    padding: 8px;
    border: none;
    margin-top: 10px;
}

.sendbutton[disabled] {
background-color : rgb(120, 163, 129);
}
.sendbutton[disabled]:hover {
    background-color : rgb(120, 163, 129);
    }
 .sendbutton:hover {
    background: rgb(136, 233, 107);
}

.editbtn {
    background-color : rgb(241, 124, 13);
    color: #FFF;
    padding: 8px;
    border: none;
    margin-top: 10px;
}

 .editbtn:hover {
    background: rgb(240, 140, 59);
}



/* MEDIA QUERIES */

@media screen and (min-width: 320px) and (max-width: 932px) {
    
    	/* Force table to not be like tables anymore */
	table, thead, tbody, th, td, tr { 
		display: block; 
	}
	
	/* Hide table headers (but not display: none;, for accessibility) */
	thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
	
	tr {
         border: 1px solid rgba(204, 204, 204, 0.808); 
         margin: 15px;
        }
	
	td { 
		/* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid #eee; 
		position: relative;
        padding-left: 50%; 
        min-height: 33px;
	}
	
	td:before { 
		/* Now like a table header */
		position: absolute;
		/* Top/left values mimic padding */
		top: 6px;
		left: 6px;
		width: 45%; 
		padding-right: 10px; 
		white-space: nowrap;
	}
	
	/*
	Label the data
	*/
	td:nth-of-type(1):before { content: "Id utilisateur"; }
	td:nth-of-type(2):before { content: "Admin"; }
	td:nth-of-type(3):before { content: "Nom user"; }
	td:nth-of-type(4):before { content: "Prenom user"; }
	td:nth-of-type(5):before { content: "Salle de sport"; }
	td:nth-of-type(6):before { content: "Ville"; }
	td:nth-of-type(7):before { content: "Pseudo"; }
    td:nth-of-type(8):before { content: "Mail"; }
   


}


