/* Style component bannerGuide */ 

.bannerguide{
    background-color: #F7F7F7;
    font-family : 'Helvetica';
}


.bloctext {
    padding: 30px;
}
.title{

    text-align:center;
    font-family : 'Helvetica';
    font-weight : bold;
    letter-spacing : 5;
    font-size: 35px;
    color : #22313F;

}

.subtitle{
    text-align: center;
    font-family : 'Helvetica';
    font-size : 30px;
    color:#22313F;
}

/* Style component bannerImg */ 



.bannerimg{
    background-color: #6BB9F0;
}

.titlebannerimg{

    font-family : 'OpenSans-light, Arial, Helvetica, sans-serif';
    letter-spacing : 5px;
    font-size: 30px;
    color : #FFF;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    padding : 50px;
}

.cta {
    background-color :white;
    color : #6BB9F0;
    margin-top:20px;
    border-radius : 4px;
    padding : 15px;
    text-align: center;
    font-size: 30px;
}

/* MEDIA QUERIES */

@media screen and (min-width: 320px) and (max-width: 640px) {
    .titlebannerimg {
      letter-spacing: 3px;
      padding: 25px;
      font-size: 20px;
      text-align: left;
    }

    .title{
      font-size: 25px;
    }
    
    .subtitle{
      font-size : 20px;
    }
}


