/* style component Search */

.item{
    margin : 15px;
}

header {

 height: 500px;
 background: -webkit-gradient(
    linear, 
    left top, left bottom, 
    from(rgba(15, 63, 78, 0.863)), 
    to(rgba(11, 38, 46, 0.753))
    ), url(../../images/gym.jpg);
 background: -webkit-linear-gradient(
    rgba(15, 63, 78, 0.863), 
    rgba(11, 38, 46, 0.753)
    ), url(../../images/gym.jpg);
 background: -o-linear-gradient(
    rgba(15, 63, 78, 0.863), 
    rgba(11, 38, 46, 0.753)
    ), url(../../images/gym.jpg);
 background: linear-gradient(
    rgba(15, 63, 78, 0.863), 
    rgba(11, 38, 46, 0.753)
    ), url(../../images/gym.jpg);
 background-size:cover;
}


.input {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 70px;
}

.inputstyle {
    border: none;
    background-color: #FFF; 
    border-left: 7px #6BB9F0 solid;
    height: 30px;
}

.items {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 500px; 
    margin: auto; 
    margin-top: 20px;
    padding: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.button {
    background: #F7CA18;
    color: #FFF;
    padding: 8px;
    border: none;
}

.button:hover {
    background: #6BB9F0;
}

a.link{
    text-decoration: none;
    color: #FFF;
}

a.link:hover {
    text-decoration: underline;
}

/* MEDIA QUERIES */

@media screen and (min-width: 320px) and (max-width: 640px) {
    header {
        height: 400px;
    }
    .items {
        width: 260px;
    }
    h1 {
        padding: 15px;
    }
    
  }


