#bandeau {
    background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(rgba(72, 242, 248, 0.8)),
		to(rgba(226, 248, 252, 0.7))
        ), url(../../../images/gym.jpg);
    background: -webkit-linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
        ), url(../../../images/gym.jpg);
    background: -o-linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
        ), url(../../../images/gym.jpg);
    background: linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
        ), url(../../../images/gym.jpg);
        /* background-size: 70%;  */
    height: 80px;
    padding: 20px; 
    text-align: right;
}



#pp {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin-left: 30px;
    z-index: 1;
    position: absolute;
    top: 10rem;
    border: 3px solid white;
}
#conteneur {
    background-color: #F7F7F7;
    padding: 1rem;
    text-align: center;
    width: 100%;
    position: relative;
}

#conteneur h1 {
    font-weight: 900;
    font-size: 22px;
}
