.infoperso {
    color: #22313F;
}

#btn{
    -ms-flex-item-align: center;
        align-self: center;
}

.editbutton i  {
    display: none !important;
}
.editbutton {
    background-color: #F7CA18;
    text-shadow: 0 0 0.2em rgb(85, 68, 2);
    /* font-weight: 900;  */
    color: #FFF;
    padding : 7px;
    border-radius: 15px;
    height: 20px;
    /*argin-left: 70rem;*/
    margin-right: 5px;
}

.editbutton:hover {
    background-color: #22313F;
}

#param {
    /* height: 400px; */
    text-align: left;
    padding: 47px;
    color: #22313F;
}

#param p {
    text-align: left;
}

.section {
    margin-top: 40px;
}

.section h2 {
    font-weight: bold;
    margin: 20px 0;
}


.section h2, .section p {
    padding: 0;
}

.section h3 {
    margin-bottom: 10px;
}


.inputgroup {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 30VW;
    margin: 10px auto;
}

.inputgroup p {
margin-bottom: 1px;
}
.deletebtn {
    background-color : rgb(250, 9, 9);
    color: #FFF;
    padding: 8px;
    border: none;
    margin-top: 10px;
}

 .deletebtn:hover {
    background: rgb(243, 74, 74);
}


.inputs {
    border: 1px #6BB9F0 solid;
    background-color: #FFF; 
    height: 20px;
    margin-bottom : 5px;

}
.inputgroup {
    /* background-color: peachpuff; */
}
#btn {
    margin: 15px auto;
}



/* MEDIA QUERIES */

@media screen and (min-width: 320px) and (max-width: 523px) {
    #bandeau {
        background-color: #6BB9F0;
        height: 50px;
        padding: 20px;
        text-align: right;
    }
    #pp {
        width: 100px;
        height: 100px;
        margin-left: 15px;
        top: 5rem;
    }


.editbutton i  {
    display: inline !important;
    font-size: 5px;
}
.editbutton span {
    display: none;
}
.editbutton {
    background-color: transparent;
    text-shadow: 0 0 0 transparent;
    padding : 0;
    border-radius:0;
    height:auto;
    margin-right: 5px;
}
.editbutton:hover{
    background-color: transparent;
}

.inputgroup {
    width: 60VW;
    margin: 50px auto 20px auto;
}



