/* style modal login */ 

.modalDialog {
	position: fixed;
	font-family: Arial, Helvetica, sans-serif;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(rgba(72, 242, 248, 0.8)),
		to(rgba(226, 248, 252, 0.7))
		), url(../../../images/gym.jpg);
	background: -webkit-linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
		), url(../../../images/gym.jpg);
	background: -o-linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
		), url(../../../images/gym.jpg);
	background: linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
		), url(../../../images/gym.jpg);
	 background-size:cover;
	z-index: 3;
	opacity:1;
	-webkit-transition: opacity 400ms ease-in;
	-o-transition: opacity 400ms ease-in;
	transition: opacity 400ms ease-in;
}



.modalDialog:target {
	opacity:1;
	pointer-events: auto;
}
.modalDialog > div {

	position: relative;
	margin: 20vh auto;
	/*padding: 5px 20px 13px 20px;*/
	border-radius: 7px;
   /* background: rgba(255,255,255,0.4); */
   background-color: white;
}

.modal {
    width : 25vw;
    height : 50vh;
	position: relative;  
}

.iconclear {
    position:absolute;
    top: 10px;
    right: 10px;
}

.formlogin{
    width : 20VW;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-direction : column;
        flex-direction : column;
	/*margin: 10rem auto;*/
	margin: auto;  
}

.inputregister {
	margin-bottom : 10px;
	padding: 7px;  
	border: 1px solid #D3D3D3;

}
.inputregister:required {
	-webkit-box-shadow: none;
	        box-shadow: none;
}
#register {
padding: 5rem 0;
}
#register a {

	text-align: center;
	color: #22313F;
	margin-top: 10px;
}

.confirm {
	border-radius: 5px;
	display: block;
	text-align: center;
	position: absolute;
	background: #22313F;
	left: 105%; 
	top: 205px;
	width: 160px;
	padding: 7px 10px;
	color: #fff;
  }
.confirm:after {
	right: 100%;
	top: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-color: rgba(136, 183, 213, 0);
	border-right-color: #22313F;
	border-width: 8px;
	margin-top: -8px;
  }
p{ 
	text-align: center;
}
#passwordcontraints {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	font-size: 12px;
}

#passwordcontraints li {
	margin-top: 10px;
}

.validate {
	color: rgba(0, 128, 0, 0.863);
	text-decoration: line-through;
}

@media screen and (min-width: 320px) and (max-width: 640px) {
	.modal {
		width: 80VW;
		height: auto;
	}
	.modalDialog > div {
		margin: 10vh auto;
	}
	.formlogin{
		width : 70VW;
	}
	#passwordcontraints {
		font-size: 10px;
	}
	
}


