.headercontact{
	font-family: Arial, Helvetica, sans-serif;
	background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(rgba(72, 242, 248, 0.8)),
		to(rgba(226, 248, 252, 0.7))
		), url(../../../images/gym.jpg);
	background: -webkit-linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
		), url(../../../images/gym.jpg);
	background: -o-linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
		), url(../../../images/gym.jpg);
	background: linear-gradient(
		rgba(72, 242, 248, 0.8),
		rgba(226, 248, 252, 0.7)
		), url(../../../images/gym.jpg);
     background-size:cover;
     text-align: center;
     color: #FFF;
}

.bloctextheader {
  padding: 20px;
}

.bloctextheader h1 {
    font-size: 50px;
    font-weight: bold;
    text-transform: uppercase;
}

.bloctextheader p {
    font-size: 20px;
    color : #22313F;
    margin-top: 15px;
}

#contactform {
    width : 40VW;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-direction : column;
        flex-direction : column;
	margin: 5rem auto;
}

/* MEDIA QUERIES */

@media screen and (min-width: 320px) and (max-width: 640px) {
    .bloctextheader p {
        font-size: 20px;
        margin-top: 10px;
    }
    .bloctextheader h1 {
          font-size: 25px;
    }
    #contactform {
        width : 70VW;
    }
   
}



