/* Style component Navigation */ 

.main {
    width: 100%;
    position: relative;
    border-bottom: 3px solid  skyblue;

}

.nav{
    width: 90%;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.nav ul {
    margin: 0px;
    padding: 0px;
    background: #FFF;
    list-style:none;
}

.nav a {
    padding: 1em;
    display: block;
    text-decoration: none;
    color : #22313F;
}
.nav .menu {
    display: -ms-flexbox;
    display: flex; /*1*/
    -ms-flex-direction : row;
        flex-direction : row; /*2*/
    -ms-flex-pack: end;
        justify-content: flex-end; /*3*/

}


.navitems{
    -ms-flex-align: center;
        align-items: center;
}

.logo img {
    margin-left: 20px;
}

/* drop down nav */ 

.dropdown {
    position: relative;
    display: inline-block;
}

.menuDeroulant {
  display: none;
  position: absolute;
  top : 4rem;
  background-color: #f1f1f1;
  min-width: 160px;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.menuDeroulant li a {
   color: black;
   padding: 16px 16px;
   text-decoration: none;
   display: block;
}

.menuDeroulant li a:hover {
    background-color: #ddd;

}

.dropdown:hover .menuDeroulant {
    display: block;
}

@media screen and (min-width: 320px) and (max-width: 640px) {
    .nav a{
        padding: 4px;
    }
    .logo img {
     margin-left: 0;
     width: 50px;
    }
}





